//main: style.less
.opinion {
	flex-direction: column;
	width: 441px;
	padding-top: 30px;
	background: #fff;
	@media screen and (max-width: 767px) {
		width: auto;
	}

	.contact-form__field {
		padding-left: 25px;

		label {
			width: 100%;
		}

		span {

			font-size: 12px;
			line-height: 18px;
			color: #666666;

			a {
				color: $base;
			}
		}
	}

	.ratings__section {
		display: inline-block;
		position: relative;
		width: 120px;
		height: 20px;
		margin: 0 auto;
		vertical-align: middle;
		cursor: pointer;
	}

	.ratings__start, .ratings__click, .ratings__disable, .ratings__hover {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		background-size: 24px !important;
	}

	.ratings__start, .ratings__disable {
		z-index: 1;
		width: 100%;
		background: url("data:image/svg+xml;base64,") repeat-x;
	}

	.reviews-list__col_desc .ratings__start, .reviews-list__col_desc .ratings__disable, .ratings__start .reviews-all__wrap, .ratings__disable .reviews-all__wrap {
		background: url("data:image/svg+xml;base64,") repeat-x;
	}

	.ratings__disable {
		display: none;
		z-index: 3;
	}

	.ratings__click, .ratings__hover {
		background: url("../images/star.svg") 0/24px repeat-x !important;
	}

	.ratings__click {
		z-index: 2;
	}

	.ratings__hover {
		display: none;
		z-index: 4;
	}

	&__title {
		color: #2e2e2e;

		font-size: 24px;
		font-weight: 500;
		line-height: 24px;
		text-align: center;
		margin-bottom: 1rem;
	}

	&__name {
		color: #2e2e2e;

		font-size: 14px;
		font-weight: 500;
		line-height: 24px;
		text-align: center;

		margin-bottom: 10px;
	}

	&__review {
		display: inline-block;
		vertical-align: middle;
		color: #666666;
		font-size: 14px;
		font-weight: 400;
		padding-left: 30px;
		margin-right: 20px;
		text-align: center;

		&:after {
			content: "*";
			color: red;
			font-size: 15px;
		}
	}

	&__form {
		padding: 1rem;
		background-color: #fff;
		text-align: left;
	}

	&__block {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;
	}

	&__field {
		box-sizing: border-box;
		width: 100%;
		border: 1px solid #cbcbcb;
		padding-left: 15px;
		text-align: left;
		background-color: white;
		color: #666666;

		font-size: 12px;
		line-height: 18px;


		&-wrapper {
			display: flex;
			justify-content: space-between;
			position: relative;
			width: 100%;
			vertical-align: top;
			margin-bottom: 15px;
			border-radius: 2px;
			background-color: #ffffff;

			&_req {
				width: 45%;
				height: 46px;
			}

			label {
				display: none;
			}

			&:last-child {
				margin-right: 0;
			}

			&+.opinion__field-wrapper {
				margin-right: 0;
			}
		}
	}

	&__label {
		display: block;
		color: #666666;
		padding-top: 6px;
		// font-family: Arial,sans-serif;
		font-size: 14px;
		font-weight: 400;
		margin-bottom: 10px;

		.opinion__field-wrapper_req & {

			&:after {
				content: "*";
				display: inline-block;
				color: red;
				font-size: 15px;
				margin-left: 5px;
			}
		}
	}

	&__textarea {
		box-sizing: border-box;
		width: 100%;
		height: 72px;
		border: 1px solid #cbcbcb;
		padding: 10px 15px;
		text-align: left;
		resize: none;
		color: #666666;

		font-size: 12px;
		line-height: 18px;
	}

	&__checkbox {
		input {
			margin-right: 5px;
		}

		b {
			color: #000000;
			font-weight: 400;
			font-size: 12px;
		}

		a {
			font-size: 12px;
		}
	}

	&__submit {
		display: block;
		position: relative;
		z-index: 2;
		height: 46px;
		margin: 0 auto;
		border: none;
		padding: 0 30px;
		text-decoration: none;
		background-color: $base;
		border-radius: 3px;
		color: #fff;
		font-size: 18px;
		text-transform: uppercase;
		line-height: 41px;
		margin-top: 25px;

		&:hover {}
	}
}
