.menu {
	display: flex;
	display: flex;
	flex-shrink: 1;
	align-items: center;
	// width:890px;
	width: 100%;

	&-mobile {
		display: none;
	}

	@media (max-width: 767px) {
		display: none;

		.city-change {
			display: flex;
			align-items: center;
			order: -2;
			z-index: 9;
			width: 100%;
			border-top: 1px solid #f1f1f1;
			padding-top: 1em;
			padding-left: 0;
			margin-right: 0;
			color: $base;
			font-size: 16px;
			font-weight: 400;
			line-height: 22px;
			text-align: left;

			&:before {
				content: "";
				width: 24px;
				height: 24px;
				background: url("../images/location.svg");
				margin-right: 8px;
			}

			&__title {
				color: #4b4b4b;
				font-size: 13px;
				font-weight: 400;
				line-height: 22px;
				margin-right: 5px;
			}

			&__bullet {
				background: url("https://icongr.am/entypo/chevron-right.svg?color=a6a6a6") 100%/14px no-repeat;
				display: flex;
				height: 14px;
			}

			&__link {
				font-size: 13px;
				font-weight: 400;
				line-height: 22px;
				margin-right: 5px;
			}

			&.active {
				.city-change__popup {
					top: calc(100% + 3px);
					left: 5px;
					min-width: unset !important;
				}
			}

			&.active {
				.city-change__popup {}
			}

			&+.city-change {
				display: none;
			}
		}

		.header__telephones {
			display: flex;
			display: none;
			flex-wrap: wrap;
			align-items: baseline;
			order: 3;
			position: relative;
			height: unset;
			max-height: 125px;
			margin-top: 0;
			padding-top: 1rem;
			padding-bottom: 0;
			margin-bottom: auto;

			&:nth-of-type(4) {
				display: none;
			}
		}

		.header__block_bottom & {
			display: flex;
			display: none;
			flex-direction: column;
			width: 100%;
		}

		.dropdown-menu__back {
			font-size: 12px !important;
			line-height: 3.75 !important;
		}

		.header__phone {
			margin-bottom: 5px;
			color: #333333;
			font-size: 18px;
			font-weight: 400;
			line-height: 26px;
		}

		.time-work {
			display: block;
			color: #666;
			font-size: 13px;
			line-height: 22px;
			text-align: left;

			i {
				color: #666;
				font-size: 13px;
				line-height: 22px;
				text-align: left;
				font-style: normal;
				margin-right: 4px;
			}
		}

		.time-work__email {
			display: flex;
			color: $base;
			font-size: 13px;
			font-weight: 400;
			line-height: 22px;
			margin-top: 8px;

			i {
				display: none;
			}
		}

		.abuse__button {
			display: flex;
			align-items: center;
			justify-content: center;
			order: 10;
			width: 100%;
			border: 1px solid $base;
			color: $base;
			font-size: 14px;
			font-weight: 400;
			line-height: 24px;
			text-transform: uppercase;
			padding-top: 5px;
			padding-bottom: 5px;
			text-align: center;
			border-radius: 4px;
			margin-top: 17px;
			margin-top: 27px;
		}

		&__list {
			display: none;
			flex-wrap: wrap;
			flex-direction: row;
			justify-content: space-between;
			order: 9;
			width: calc(100% + 32px);
			padding: 7px 16px 24px;
			background-color: #f1f1f1;
		}

		&__item {
			width: 40%;
			margin-top: 17px;
			cursor: pointer;
			margin-left: 0 !important;

			a {
				white-space: nowrap;
				color: #666666 !important;
				font-weight: 400 !important;
			}
		}

		nav {
			display: flex;
			flex-wrap: wrap;
		}

		&.active {
			display: flex;
			width: inherit;
			margin: 0 -16px;
			padding: 0 16px 10vh;
			background: #f6f6f6;
			// transform: scale(1);
			// z-index: 10;
			// background-color: #262626;
		}

		&+.menu {
			display: none;
		}

		&-mobile {
			display: flex;
			flex-direction: column;
			background: #f6f6f6;
			min-width: unset;
			margin-top: 18px;

			// .menu-mobile__tel
			&__tel {
				font-family: "HelveticaNeueCyr-Light";
				font-size: 24px;
				font-weight: 400;
				line-height: 26px;
				text-align: center;
				color: #000000;
				margin-bottom: 4px;
			}

			// .menu-mobile__text
			&__text {
				display: flex;
				color: #4b4b4b;
				font-size: 13px;
				font-weight: 400;
				line-height: 22px;
				text-align: center;
				margin-bottom: 16px;
				justify-content: center;
			}

			// .menu-mobile__call-phone-button
			&__call-phone-button {
				font-size: 16px;
				font-weight: 400;
				line-height: 22px;
				margin-bottom: 14px;
				display: flex;
				align-items: center;
				justify-content: center;
				text-decoration: unset !important;

				&:before {
					content: "";
					width: 14px;
					height: 14px;
					background: url(../images/footer__call.svg);
					margin-right: 4px;
					background-size: contain;
				}

				span {
					border-bottom: 1px dashed #8c0014;
				}
			}

			// .menu-mobile__col
			&__col {
				display: flex;
				flex-wrap: wrap;
				flex-direction: row;
				height: auto;
			}

			// .menu-mobile__title
			&__title {
				width: 100%;
				padding: 8px 16px;
				font-family: HelveticaNeueCyr-Medium;
				font-size: 16px;
				line-height: 36px;
				margin-bottom: 0;
				cursor: pointer;
				text-align: center;
				cursor: pointer;
				text-align: center;
				color: #000000;
			}

			// .menu-mobile__list
			&__list {
				display: block;
				flex-grow: 1;
				width: 50%;
				height: unset !important;
				margin: 0;
				padding: 0;
				padding-bottom: 20px;
				list-style-type: none;
			}

			// .menu-mobile__item
			&__item {
				display: flex;
				width: 100%;
				max-width: 100%;
				padding: 0;
				cursor: pointer;
				color: #8c0014;
				margin-bottom: 10px;
			}

			// .menu-mobile__link
			&__link {
				color: #4b4b4b;
				font-family: HelveticaNeueCyr-Roman;
				font-size: 13px;
				font-weight: 400;
				line-height: 16px;
			}
		}
	}

	.header__telephones {
		display: none;
		align-items: center;
	}

	.city-change {
		display: flex;
		position: relative;
		color: #ffffff;
		font-family: "HelveticaNeueCyr-Bold";
		font-size: 13px;
		font-weight: 400;
		line-height: 17px;
		margin-right: 48px;
		cursor: pointer;

		&__popup {
			display: none;
			// opacity: 0;
			position: absolute;
			top: calc(100% + 10px);
		}

		&__title {
			// border-bottom: 1px dashed #ffffff;
			margin-right: 5px;
			white-space: nowrap;
		}

		&__bullet {
			width: 14px;
			background: url("https://icongr.am/entypo/chevron-down.svg?color=a6a6a6") 100%/14px no-repeat;
		}

		&__link {
			white-space: nowrap;
			margin-bottom: 10px;
			text-decoration: none !important;
			color: $base;

			&:last-child {
				margin-bottom: 0;
			}
		}

		&.active {
			.city-change__popup {
				display: flex;
				flex-direction: column;
				top: calc(100% + 10px);
				left: -10px;
				min-width: 100%;
				border: 1px solid #ddd;
				// opacity: 1;
				padding: 10px;
				padding: 5px 10px;
				border-radius: 4px;
				background: white;
				box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.49);
				// box-shadow: 0 5px 9px 0 #000;
			}
		}
	}

	.abuse__button {
		display: none;
	}

	.header__block_bottom & {
		display: none;
	}

	&__list {
		display: flex;
		// justify-content: space-between;
		width: auto;
		margin: 0;
		padding: 0;
		list-style-type: none;
		// flex-grow: 1;
		margin-left: auto;
	}

	&__item {
		margin-left: 45px;

		&:hover {

			// margin-left: 40px;
			a, span {
				// color: $base;
				text-shadow: 0 0 0.9px #fff, 0 0 0.9px #fff, 0 0 0.9px #fff;
				text-decoration: none;
				color: #ffffff;
			}
		}

		&:last-child {
			padding-right: 0;
		}

		&:first-child {
			margin-left: 1px;
		}
	}

	&__link {
		display: block;
		color: #fffffff0;
		font-size: 13px;
		font-weight: 400;
		line-height: 15px;

		&.active {
			text-shadow: 0 0 0.9px #fff, 0 0 0.9px #fff, 0 0 0.9px #fff;
			pointer-events: none;
		}
	}

	* {
		transition: 0.2s;
	}

	// flex-grow: 1;
	nav {
		width: 100%;
	}
}

