.articles {
	padding: 0 0 30px 3px;
	margin-bottom: 18px;

	@media screen and (max-width: 767px) {
		padding-bottom: 0;
		position: relative;
		margin-bottom: 32px;

		.articles__items {
			margin-right: -15px;
			flex-wrap: nowrap;
		}

		.container {
			padding-top: 16px !important;
		}

		.swiper-container {
			margin-bottom: 23px;
		}

		&__items {
			flex-wrap: wrap;
		}

		&__item {
			flex-wrap: nowrap;
			// flex-grow: 1;
			flex-direction: column;
			flex-grow: 1;
			width: 238px;
			margin-right: 15px;
			margin-bottom: 14px !important;

			&:last-child {
				margin-right: 15px;
			}
		}

		&__text {
			font-family: "HelveticaNeueCyr-Light";
			font-size: 18px;
			font-weight: 400;
			line-height: 24px;
			width: inherit;
		}

		&__info {
			// display: none;
		}

		&__link {
			order: 3;
			width: 100%;
			margin-top: 0;
			text-align: center;
			height: 36px;
			padding: 3px 0 0;
			line-height: 1;
			display: flex;
			justify-content: center;
			align-items: center;
		}

		&__title {
			font-size: 20px;
			line-height: 28px;
			font-weight: 400;
			margin-bottom: 7px;
		}

		&__date {
			margin-bottom: 7px;
		}

		&__pic {
			flex-direction: column;
			align-items: baseline;
			width: 100%;
			height: auto;
			// max-height: 200px;
			margin-bottom: 15px;

			img {
				// max-height: fit-content;
				width: 100%;
				max-width: 100%;
				height: auto;
			}

			picture {
				width: 100%;
				text-align: center;
			}
		}

		&__pagination {
			display: flex;
			justify-content: center;
			top: unset;
			bottom: 60px;
			left: 0;
			width: 100%;

			.swiper-pagination-bullet {
				margin: 0;

				&:not(:last-of-type) {
					margin-right: 16px;
				}
			}
		}
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		padding-top: 0;
	}

	.navchain {
		margin-bottom: 0;
	}

	&__title {
		font-family: "HelveticaNeueCyr-Light";
		font-size: 26px;
		font-weight: 400;
		line-height: 32px;
		margin-bottom: 24px;
		line-height: 38px;
	}

	&__link {
		border: solid 1px $base;
		padding: 10px 17px;
		margin-left: auto;
		border-radius: 3px;
		font-size: 14px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: left;
		color: $base;
		text-transform: uppercase;
		text-decoration: none !important;
		display: flex;
		align-items: center;
		justify-content: center;

		&:hover {
			background: #ececec;
		}
	}

	&__items {
		display: flex;

		// justify-content: space-between;
		&.swiper-wrapper {
			justify-content: space-between;

			.swiper-slide {
				padding-left: 0;
				margin-right: 0;
			}
		}
	}

	&__item,
	.swiper-slide {
		box-sizing: border-box;
		display: flex;
		flex-wrap: wrap;
		align-content: flex-start;
		align-items: baseline;
		width: calc(25% - 20px);
		margin-right: 28px;

		&:last-child {
			margin-right: 0;
		}

		// &:hover{
		// 	box-shadow: 0px 2px 21px rgba(0,0,0,0.09);
		// }
	}

	&__pic {
		display: flex;
		order: -5;
		width: 100%;
		height: 170px;
		margin-bottom: 12px;

		img {
			width: 100%;
			max-width: 100%;
			height: auto;
			max-height: 100%;
		}

		picture {
			width: 100%;
		}
	}

	&__img {
		// margin-bottom: 16px;
	}

	&__text {
		width: 100%;
		font-family: "HelveticaNeueCyr-Light";
		font-size: 20px;
		font-weight: 400;
		line-height: 24px;
		margin-bottom: 9px;
		color: #4b4b4b;
		text-decoration: none !important;

		&:hover {
			color: $base;
		}
	}

	&__date {
		color: #4b4b4b;
		font-family: "HelveticaNeueCyr-Roman";
		font-size: 12px;
		font-weight: 400;
		line-height: 16px;
		margin-bottom: 7px;
		order: -1;
	}

	&__info {
		color: #4b4b4b;
		font-family: "HelveticaNeueCyr-Roman";
		font-size: 14px;
		font-weight: 400;
		line-height: 20px;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 4;
		overflow: hidden;

		font[color="#ff0000"] {
			color: $base !important;
		}
	}
}
